import { Chip, Stack, Typography } from '@mui/material'
import { MailingApi } from 'API/mailing'
import DialogConfirm from 'components/DialogConfirm'
import { IGroup } from 'interfaces'
import { useEffect, useState } from 'react'
import { MdAddCircleOutline } from 'react-icons/md'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'
import { UIAction } from 'redux/slices/UI'

export default function AssignGroup() {
	const nav = useNavigate()
	const { id } = useParams<{ id: string }>()
	const mailing = useAppSelector((s) => s.Mailing.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	const groups = useAppSelector((s) => s.Group.list)
	const status = useAppSelector((s) => s.Group.status)
	const dispatch = useAppDispatch()
	const [data, setData] = useState<IGroup[]>([])
	useEffect(() => {
		if (status === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		setData(groups.filter((f) => mailing && mailing.allowGroup.includes(f.id)))
	}, [groups, mailing])
	useEffect(() => {
		return () => {
			setOpen(false)
		}
	}, [])
	if (!mailing || !id) return null
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => {
				nav('/mailing/' + id)
			}}
			title="メールグループ送信可能組織設定"
			primaryButtonColor="error"
			primaryButtonText="設定する"
			handleSubmit={async () => {
				try {
					if (!id) return
					setLoading(true)
					const { message, error } = await MailingApi.update({ id, allowGroup: data.map((v) => v.id) })
					dispatch(
						UIAction.addGlobalMessage({
							message,
							type: error ? 'error' : 'success',
						})
					)
					if (!error) nav('/mailing/' + id)
				} finally {
					setLoading(false)
				}
			}}
		>
			<Stack gap={1}>
				<Typography>送信可能組織</Typography>
				<Stack
					direction="row"
					gap={1}
					sx={{
						border: '1px solid #eee',
						p: 2,
					}}
				>
					{data.map((v) => {
						return (
							<Chip
								key={v.id}
								color="success"
								label={v.name}
								variant="outlined"
								onDelete={() => setData(data.filter((f) => f.id !== v.id))}
							/>
						)
					})}
				</Stack>
				<Typography>送信不可組織</Typography>
				<Stack
					direction="row"
					gap={1}
					flexWrap="wrap"
					sx={{
						border: '1px solid #eee',
						p: 2,
					}}
				>
					{groups
						.filter((f) => !data.find((d) => d.id === f.id))
						.map((v) => {
							return (
								<Chip
									key={v.id}
									label={v.name}
									variant="outlined"
									onDelete={() => setData([...data, v])}
									deleteIcon={<MdAddCircleOutline />}
								/>
							)
						})}
				</Stack>
			</Stack>
		</DialogConfirm>
	)
}
